<template>
  <div id="app">

  <div class="form">
    
  <div class="title"><h1>Анкета клиента</h1></div>

  <!--ФАМИЛИЯ-->
    <div class="l-name">
      <label for="l-name">Фамилия<b>*</b>: </label><br>

      <span class="error-text" v-if="$v.formReg.lName.$error && $v.formReg.lName.alpha">Укажите вашу фамилию</span>
      <span class="error-text" v-if="!$v.formReg.lName.alpha">Поле не должно содержать цифр</span>

      <input 
      @blur="$v.formReg.lName.$touch()"
      :class="{'invalid': $v.formReg.lName.$error}" 
      v-model="$v.formReg.lName.$model"
      type="text" id="l-name">

    </div>
    <!--ФАМИЛИЯ-->

    <!--ИМЯ-->
    <div class="f-name">
      <label for="f-name">Имя<b>*</b>: </label><br>

      <span class="error-text" v-if="$v.formReg.fName.$error && $v.formReg.fName.alpha">Укажите ваше имя</span>
      <span class="error-text" v-if="!$v.formReg.fName.alpha">Поле не должно содержать цифр</span>

      <input
      @blur="$v.formReg.fName.$touch()"
      :class="{'invalid': $v.formReg.fName.$error}" 
      v-model="$v.formReg.fName.$model"
      type="text" id="f-name">
    </div>
    <!--ИМЯ-->

    <!--ОТЧЕСТВО-->
    <div class="patronymic">
      <label for="patronymic">Отчество: </label><br>
      <input type="text" id="patronymic">
    </div>
    <!--ОТЧЕСТВО-->

    <!--ДАТА РОЖДЕНИЯ-->
    <div class="date">
      <label for="dateBirth">Дата рождения<b>*</b>: </label><br>

      <span class="error-text" v-if="$v.formReg.dateBirth.$error">Введите дату вашего рождения полностью</span>

      <input 
      @blur="$v.formReg.dateBirth.$touch()"
      :class="{'invalidMulti': $v.formReg.dateBirth.$error}" 
      v-model="$v.formReg.dateBirth.$model"
      type="date" id="dateBirth">
    </div>
    <!--ДАТА РОЖДЕНИЯ-->

    <!--ТЕЛЕФОН-->
    <div class="tel">
      <label for="phone">Номер телефона<b>*</b>: </label><br>

      <span class="error-text" v-if="!$v.formReg.phone.minLength || $v.formReg.phone.$error">Поле должно содержать 11 цифр и начинатся с 7</span>

      <input 
      @blur="$v.formReg.phone.$touch()"
      :class="{'invalid': $v.formReg.phone.$error}" 
      v-model="$v.formReg.phone.$model"
      type="text" id="phone" maxlength="11">
    </div>
    <!--ТЕЛЕФОН-->

    <!--ПОЛ-->
    <div class="gender">
      <span>Мужчина</span>
      <input checked=true type="radio" name="gender">
      <span>Женщина</span>
      <input type="radio" name="gender">
    </div>
    <!--ПОЛ-->

    <!--ГРУППА КЛИЕНТОВ-->
    <div class="group-client">

    <span class="error-text" v-if="$v.formReg.groupClient.$error">Выберите минимум 1 пункт</span>

      <select name="group-client" multiple
      @blur="$v.formReg.groupClient.$touch()"
      :class="{'invalidMulti': $v.formReg.groupClient.$error}" 
      v-model="$v.formReg.groupClient.$model"
      >
        <option value="vip">VIP</option>
        <option value="problems">Проблемные</option>
        <option value="oms">ОМС</option>
      </select><b>*</b>
    </div>
    <!--ГРУППА КЛИЕНТОВ-->

    <!--ЛЕЧАЩИЙ ВРАЧ-->
    <div class="attending-physician">
      <label for="attending-physician">Лечащий врач: </label><br>
      <select id="attending-physician">
        <option value="ivanov">Иванов</option>
        <option value="zaharov">Захаров</option>
        <option value="chernushov">Чернышева</option>
      </select>
    </div>
    <!--ЛЕЧАЩИЙ ВРАЧ-->

    <!--НЕ ОТПРАВЛЯТЬ СМС-->
    <div class="no-sms">
      <input type="checkbox" id="sms-no">
      <label for="sms-no">Не отправлять СМС</label>
    </div>
    <!--НЕ ОТПРАВЛЯТЬ СМС-->

    <h2>Адрес</h2>

    <!--ИНДЕКС-->
    <div class="index">
      <label for="index">Индекс: </label><br>
      <input type="text" id="index">
    </div>
    <!--ИНДЕКС-->

    <!--СТРАНА-->
    <div class="country">
      <label for="country">Страна: </label><br>
      <input type="text" id="country">
    </div>
    <!--СТРАНА-->

    <!--ОБЛАСТЬ-->
    <div class="area">
      <label for="area">Область: </label><br>
      <input type="text" id="area">
    </div>
    <!--ОБЛАСТЬ-->

    <!--ГОРОД-->
    <div class="city">
      <label for="city">Город<b>*</b>: </label><br>

        <span class="error-text" v-if="$v.formReg.city.$error && $v.formReg.city.alpha">Укажите название вашего города</span>
        <span class="error-text" v-if="!$v.formReg.city.alpha">Название города не может содержать цифр</span>

      <input
      @blur="$v.formReg.city.$touch()"
      :class="{'invalid': $v.formReg.city.$error}" 
      v-model="$v.formReg.city.$model"
      type="text" id="city">
    </div>
    <!--ГОРОД-->

    <!--УЛИЦА-->
    <div class="street">
      <label for="street">Улица: </label><br>
      <input type="text" id="street">
    </div>
    <!--УЛИЦА-->

    <!--ДОМ-->
    <div class="home">
      <label for="home">Дом: </label><br>
      <input type="text" id="home">
    </div>
    <!--ДОМ-->

    <h2>Паспорт</h2>

    <!--ТИП ДОКУМЕНТА-->
    <div class="document-type">
      <label for="document-type">Тип документа: </label><br>
      <select v-on:change="changeDate" id="document-type">
        <option value="passport">Паспорт</option>
        <option value="birth-certificate">Свидетельство о рождении</option>
        <option value="drivers-license">Водительское удостоверение</option>
      </select>
    </div>
    <!--ТИП ДОКУМЕНТА-->

    <div v-if="passportVisible" class="passportWrap">

    <!--СЕРИЯ-->
    <div class="series">
      <label for="serie">Серия: </label><br>
      <input type="text" id="serie">
    </div>
    <!--СЕРИЯ-->

    <!--НОМЕР-->
    <div class="number">
      <label for="number">Номер: </label><br>
      <input type="text" id="number">
    </div>
    <!--НОМЕР-->

    <!--КЕМ ВЫДАН-->
    <div class="issued">
      <label for="issued">Кем выдан: </label><br>
      <input type="text" id="issued">
    </div>
    <!--КЕМ ВЫДАН-->

    <!--ДАТА ВЫДАЧИ-->
    <div class="date-of-issue">
      <label for="dateIssue">Дата выдачи<b>*</b>: </label><br>

      <span class="error-text" v-if="$v.formReg.dateIssue.$error">Введите дату выдачи паспорта полностью</span>

      <input
      @blur="$v.formReg.dateIssue.$touch()"
      :class="{'invalidMulti': $v.formReg.dateIssue.$error}" 
      v-model="$v.formReg.dateIssue.$model"
      type="date" id="dateIssue">
    </div>
    <!--ДАТА ВЫДАЧИ-->

    </div>

    <button :disabled="disabledBtn" v-on:click="clickBtn" type="button" class="send-btn">Отправить</button>

    <div v-if="this.sucefullVisible" class="sucefull">
        <p>{{sucefullText}}</p>
    </div>

    </div>

  </div>
</template>

<script>

import { required, helpers, minLength } from 'vuelidate/lib/validators'

const alpha = helpers.regex('alpha', /^[a-zA-Zа-яА-ЯЁё]*$/)

const numbAlpha = helpers.regex('alpha', /^[7]+[0-9]*$/)


export default {
  data() {
    return {
      formReg: {
        lName: '',
        fName: '',
        dateBirth: '',
        phone: '',
        typePatient: '',   
        city: '',
        dateIssue: '',
        groupClient: []
      },
      sucefullVisible: false,
      passportVisible: true,
      sucefullText: 'Регистрация выполнена успешно'
    }
  },
  computed: {
    disabledBtn: function() {
      return this.$v.formReg.lName.$invalid ||
             this.$v.formReg.fName.$invalid ||
             this.$v.formReg.dateBirth.$invalid ||
             this.$v.formReg.groupClient.$invalid ||
             this.$v.formReg.city.$invalid ||
             this.$v.formReg.phone.$invalid ||
             (this.$v.formReg.dateIssue.$invalid && this.passportVisible === true)
    }
  },
  methods: {
    clickBtn: function() {
      this.sucefullVisible = true,
      document.querySelector('.send-btn').parentElement.removeChild(document.querySelector('.send-btn'))
      setTimeout(function() {
          location.reload()
        }, 5000)
    },
    changeDate: function(event) {
      if(event.target.value === 'passport') {
        this.passportVisible = true
      } else if (event.target.value === 'birth-certificate') {
        this.passportVisible = false
      } else {
        this.passportVisible = false
      }
    }
  },
  validations: {
    formReg: {
      lName: {
        required,
        alpha
      },
      fName: {
        required,
        alpha
      },
      date: {
        required
      },
      phone: {
        required,
        numbAlpha,
        minLength: minLength(11)
      },
      dateBirth: {
        required
      },
      typePatient: {
        required
      },
      city: {
        required,
        alpha
      },
      dateIssue: {
        required
      },
      groupClient: {
        required
      }
    }
  }
}
</script>

